import React from 'react'
import PureRenderMixin from 'react-addons-pure-render-mixin'
import {
    connect
} from 'react-redux'
import {
    bindActionCreators
} from 'redux'
import {
    timeFormat
} from 'utils/date.js'
import {
    getHomesData
} from 'actions/Home/getHomeData.js'

import {
    BackTop,
    Popover,
    Row,
    Col,
    Progress
} from 'antd';
import './style.scss'

import Header from './subpage/Header/index.js'
import Loading from "components/Loading/index.js"
import Tables from 'components/Home/Tables/index.js'
import Footer from 'components/Home/Footer/index.js'

import Pic from "images/pic.png"
import ImgTable from 'images/img-table.png'
import Xinlingshou from 'images/xinlingshou.png';
import NumberCul from 'images/numberCulture.png';
import EcologyLogo from 'images/ecology.png';
import OverPay from 'images/overPay.png';
import Watch from 'images/watch.png';
import Exchange from 'images/exchange.png';
import Network from 'images/network.png';

class Home extends React.Component {
    constructor(props) {
        super(props);
        this.shouldComponentUpdate = PureRenderMixin.shouldComponentUpdate.bind(this);
        this.state = {
            productList: [{
                img: Xinlingshou,
                charac: ""
            }, {
                img: NumberCul,
                charac: ""
            }, {
                img: OverPay,
                charac: ""
            }, {
                img: Watch,
                charac: ""
            }],
            advantage: [
                {
                    img: Exchange,
                    title: "",
                    desc: [
                        "",
                        "2020"
                    ]
                },
                {
                    img: Network,
                    title: "",
                    desc: [
                        "AI",
                        '',
                        '',
                        'Token'
                    ]
                }
            ]
        };
    }


    getTableColumns = () => {
        const columns = [{
            title: '',
            dataIndex: 'node',
            key: 'node'
        }, {
            title: '',
            dataIndex: 'txid',
            key: 'txid',
        }, {
            title: '',
            dataIndex: 'txInfo',
            key: 'txInfo',
            render: (text, row, index) => {

                return (
                    <Popover placement="top" title={""} content={text} trigger="hover">
                        <span>{text.substr(0, 50)}</span>
                    </Popover>
                );
            },
        }, {
            title: '',
            dataIndex: 'block',
            key: 'block',
        }, {
            title: '',
            dataIndex: 'seconds',
            key: 'seconds',
            render: (text, row, index) => {
                return timeFormat(text);
            }
        }];

        return columns;

    }

    getTableData = () => {
        const data = this.props.homesData.txRecords;

        data.map((item, index) => {
            return item.key = JSON.stringify(index);
        })


        return data;

    }

    componentWillMount() {

    }

    componentDidMount() {

    }

    handleScroll (e) {

    }

    render() {

        const percent = Number((this.props.homesData.issuedAmt / this.props.homesData.totalAmt) * 100).toFixed(0) + "%";

        const nowURTStyle = {
            marginLeft: percent
        }

        return (
            <div>
                <Header />
                <section className="section1">
                    <Row type="flex" justify="center">
                        <Col span={16}>
                            <div className="about">
                                <img className="img" src={Pic}/>
                                <div className="info">
                                    <h3 className="title"></h3>
                                    <div>
                                        <p className="elem">20169</p>
                                        <p className="elem">Linux Foundation Hyperledger</p>
                                        <p className="elem">
                                            </p>
                                    </div>
                                </div>
                            </div>
                        </Col>
                    </Row>
                    <Row type="flex" justify="center" className="ecology-content">
                        <Col span={16}>
                            <div className="ecology">
                                <h3 className="title"></h3>
                                <img className="img" src={ImgTable}/>
                            </div>
                        </Col>
                    </Row>
                </section>
                <section id="assets" className="section2">
                    <Row type="flex" justify="center" className="ecology-content">
                        <Col span={16}>
                            <h3 className="title"></h3>
                            <ul className="products">
                                {this.state.productList.map((item, index) => {
                                    return (
                                        <li key={index}>
                                            <img src={item.img}/>
                                            <div className="name">{item.charac}</div>
                                        </li>
                                    )
                                })}
                            </ul>
                        </Col>
                    </Row>
                </section>
                <section id="assets" className="section3">
                    <Row type="flex" justify="center">
                        <Col span={16}>
                            <div className="products">
                                <h3 className="title"></h3>
                                <div className="products-content">
                                    {this.state.advantage.map((item, index) => {
                                        return (
                                            <div key={index} className="item">
                                                <img className="img" src={item.img}/>
                                                <div>
                                                    <h3 className="name">{item.title}</h3>
                                                    <ul className="desc">
                                                        {item.desc.map((elem, i) => {
                                                            return <li key={i}>{elem}</li>
                                                        })}</ul>
                                                </div>
                                            </div>
                                        )
                                    })}
                                </div>
                            </div>
                        </Col>
                    </Row>
                </section>
                <Footer />
                <BackTop>
                    <div className="ant-back-top-inner">UP</div>
                </BackTop>
                {/* <Loading /> */}
            </div>

        )
    }
}

function mapStateToProps(state) {
    return {
        homesData: state.homesData
    }
}

function mapDispatchToProps(dispatch) {
    return {
        getHomesData: bindActionCreators(getHomesData, dispatch)
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Home)


